@import "../../common/styles/sass/variablies.sass";
@import "../../common/styles/sass/mixins.sass";

.project {
  margin: 20px;
  width: 70vh;
  background-color: $bgSecondColor;
  color: $textColor;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: start;
  border-radius: 2px;
  .image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-size: cover;
    background-position: center;
    height: 220px;
    position: relative;
    .projectBtn {
      background-color: $primarycolor;
      text-decoration: none;
      color: black;
      opacity: 0.7;
      font-size: 15px;
      font-weight: 700;
      padding: 12px 40px;
      border: none;
      cursor: pointer;
      border-radius: 5px;
      &:hover {
        background-color: $primarycolor;
        opacity: 1;
      }
    }
  }
  .projectInfo {
    padding: 15px;
    .nameProject {
      margin-bottom: 5px;
      @include text($titleFontFamily, 22px, 700, 1px);
    }
    .description {
      @include text($textFontFamily, 16px, 400, 1px);
    }
  }
}
